"use strict";
var bars = document.getElementsByClassName('progress');
for (var i = 0; i < bars.length; i++) {
    var percentage = bars[i].dataset.percentage;
    var degrees = 360 / 100 * percentage;
    var right = bars[i].getElementsByClassName('progress-right')[0].getElementsByClassName('progress-bar');
    var left = bars[i].getElementsByClassName('progress-left')[0].getElementsByClassName('progress-bar');
    if (percentage <= 50) {
        right[0].style.transform = 'rotate(' + degrees + 'deg)';
        left[0].style.transform = 'rotate(0deg)';
    }
    else {
        right[0].style.transform = 'rotate(180deg)';
        left[0].style.transform = 'rotate(' + degrees / 2 + 'deg)';
    }
}
